.tooltip__container {
    width: 430px;
    min-height: 330px;
    margin: 0 auto;
    background-color: #ffffff;
    position: fixed;
    top: calc(100%/2 - 165px);
    left: calc(100%/2 - 215px);
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
}

@media screen and (max-width: 540px) {
    .tooltip__container {
        width: 280px;
        min-height: 308px;
        top: calc(100%/2 - 161px);
        left: calc(100%/2 - 141px);
    }
}