.auth__input {
    display: flex;
    padding: 0;
    margin: 0 36px 0;
    width: 358px;
    min-height: 27px;
    font-family: 'Inter', sans-serif;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    color: #ffffff;
    border: none;
    background-color: #000000;
    border-bottom: 2px solid #CCCCCC;
}

@media screen and (max-width: 540px) {
    .auth__input {
        width: 238px;
        margin: 0 22px 0;
    }
}