.auth {
    width: 430px;
    min-height: 330px;
    margin: 0 auto;
    border-radius: 10px;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
}

@media screen and (max-width: 540px) {
    .auth {
        width: 282px;
        min-height: 322px;
        top: calc(100%/2-161px);
        left: calc(100%/2-141px);
    }
}